.date-box {
  display: flex;
  align-items: center;
  gap: 32px;
  justify-content: center;
  margin-top: 12px;
  background: var(--bg-medium);
  border: 2px solid var(--text-light);
  border-radius: 18px;
  padding: 18px 32px;
}
.date-box label {
  font-size: 22px;
  color: var(--accent-cyan);
  font-family: 'Montserrat', sans-serif;
  margin-right: 12px;
}
.date-box select,
.date-box input[type='date'],
.date-box select {
  font-size: 22px;
  padding: 10px 18px;
  border-radius: 12px;
  border: none;
  background: var(--bg-dark);
  color: var(--text-light) !important;
  font-family: 'Roboto Mono', monospace;
  margin-right: 24px;
}
.date-box .save-date {
  font-size: 22px;
  padding: 10px 18px;
  border-radius: 12px;
  border: none;
  background: var(--bg-dark);
  color: var(--text-light);
  font-family: 'Roboto Mono', monospace;
  margin-right: 24px;
}
.topbar-dates {
  display: flex;
  align-items: center;
  gap: 32px;
  justify-content: center;
  margin-top: 12px;
}
.topbar-dates label {
  font-size: 22px;
  color: var(--text-light);
  font-family: 'Montserrat', sans-serif;
  margin-right: 12px;
}
.topbar-dates select,
.topbar-dates input[type='date'] {
  font-size: 22px;
  padding: 10px 18px;
  border-radius: 12px;
  border: none;
  background: var(--bg-light);
  color: var(--text-light);
  font-family: 'Roboto Mono', monospace;
  margin-right: 24px;
}

/* date field: label above and visible porcelain box for the date */
.date-field {
  display: flex;
  flex-direction: column;
  align-items: center; /* center label and input */
  gap: 6px;
}
.date-field label {
  font-size: 16px;
  color: var(--accent-blue);
  font-family: 'Montserrat', sans-serif;
  font-weight: 700;
  text-align: center;
  width: 100%;
}
.date-field input[type='date'] {
  background: var(--bg-light);
  color: var(--bg-dark) !important;
  border: 1px solid rgba(255,255,255,0.2);
  padding: 12px 16px;
  border-radius: 10px;
  font-size: 20px;
  font-weight: 700;
  text-align: center;
  min-width: 180px;
}

.today-btn {
  background: var(--accent-cyan);
  color: var(--bg-dark);
  border: none;
  padding: 8px 12px;
  border-radius: 8px;
  font-weight: 700;
  cursor: pointer;
}

.date-field > div {
  display: flex;
  align-items: center;
  gap: 8px;
}
.date-field > div input[type='date'] {
  /* ensure inside grouped div keeps same size */
  min-width: 160px;
}
:root {
  --bg-dark: #1E2329;
  --bg-darker: #15181E;
  --bg-medium: #2A2F36;
  --bg-light: #ebeeef; /* Porcelain */
  --text-light: #FFFFFF;
  --text-gray: #B0B0B0;
  --text-muted: #888888;
  --accent-cyan: #00D1FF;
  --accent-blue: #06a3b8; /* Eastern Blue */
}
body {
  background-color: var(--bg-dark);
  color: var(--text-light);
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
}
* {
  transition: all 0.2s ease-in-out;
}
.sidebar {
  background-color: var(--bg-medium);
  width: 220px;
  min-height: 100vh;
  padding: 20px;
  box-shadow: 2px 0 10px rgba(0, 0, 0, 0.3);
  position: relative; /* stacking context di default */
  z-index: 20;
}
.sidebar h1, .sidebar h2, .sidebar h3 {
  color: var(--text-light);
  font-family: 'Montserrat', sans-serif;
}
.sidebar a {
  color: var(--text-gray);
  text-decoration: none;
  display: block;
  margin: 10px 0;
  transition: color 0.3s;
}
.sidebar a:hover {
  color: var(--accent-cyan);
}
.topbar {
  background-color: var(--bg-medium);
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 16px 32px;
  border-bottom: 1px solid var(--bg-darker);
}
.topbar h1 {
  color: var(--text-light);
  font-family: 'Montserrat', sans-serif;
  font-size: 18px;
  letter-spacing: 1px;
}
.topbar p {
  color: var(--text-gray);
  font-size: 14px;
  opacity: 0.8;
}
.kpi-container {
  display: flex;
  gap: 24px;
  margin: 24px;
}
.kpi-card {
  background-color: var(--bg-medium);
  border-radius: 8px;
  flex: 1;
  padding: 16px 24px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.3);
}
.kpi-title {
  color: var(--text-muted);
  font-size: 12px;
  text-transform: uppercase;
}
.kpi-value {
  color: var(--accent-cyan);
  font-family: 'Roboto Mono', monospace;
  font-size: 28px;
  font-weight: bold;
}
.chart-container {
  background-color: var(--bg-darker);
  border-radius: 8px;
  padding: 16px;
  margin: 16px;
  box-shadow: 0 2px 8px rgba(0,0,0,0.2);
}
.chart-title {
  color: var(--text-light);
  font-size: 14px;
  margin-bottom: 12px;
}

/* Donut charts row: centered and responsive so donuts move together with top tabs and charts */
.donut-row {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  gap: 48px;
  margin: 12px 0 20px;
  flex-wrap: wrap; /* garantisce che le card vadano a capo */
  width: 100%;
  box-sizing: border-box;
  margin-bottom: 24px; /* spazio extra per separare dai grafici sottostanti */
}

/* rendi le card responsive: possono ridursi ma mantengono proporzioni */
.donut-card {
  flex: 0 1 280px;       /* non cresce, può ridursi, base 280px */
  box-sizing: border-box;
  min-width: 160px;      /* limite inferiore (corrisponde a media query) */
  max-width: 280px;
  aspect-ratio: 1 / 1;   /* mantiene proporzione quadrata -> circolare con border-radius */
  height: auto;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 0 8px 24px rgba(0,0,0,0.35);
}

/* prevenire che le donut si sovrappongano visivamente ai grafici: */
.donut-row, .donut-card {
  position: relative;
  z-index: 0;
}

.chart-container {
  position: relative; /* crea nuovo stacking context */
  z-index: 1;         /* garantisce che i grafici appaiano sopra se c'è ancora sovrapposizione */
  min-width: 0;       /* ulteriore protezione contro overflow nei flex */
}

/* consenti ai contenitori flessibili interni di ridursi correttamente */
.kpi-container, .chart-container {
  min-width: 0; /* evita overflow dei figli in flex */
}

/* media queries aggiornate (mantieni coerenti con le tue dimensioni) */
@media (max-width: 1100px) {
  .donut-card { flex-basis: 220px; max-width: 220px; min-width: 140px; }
  .donut-row { gap: 28px; }
}

@media (max-width: 760px) {
  .donut-card { flex-basis: 160px; max-width: 160px; min-width: 120px; box-shadow: none; }
  .donut-row { gap: 16px; }
}

/* Sidebar overlay su schermi piccoli */
@media (max-width: 860px) {
  .sidebar {
    position: fixed;
    top: 0;
    left: 0;
    height: 100vh;
    z-index: 600; /* sopra il main, sotto i modal (1000+) */
    box-shadow: 0 6px 24px rgba(0,0,0,0.45);
    transition: width 220ms ease;
  }
  main {
    padding-left: 0;
  }
}
